    <!--  轮播 -->
    <div class="lunbo">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for="(item,index) in lunbo" :key="index">
          <img :src="item" alt />
        </mt-swipe-item>
      </mt-swipe>
    </div>

    <!-- fenlei -->
    <div class="fenlei">
      <ul>
        <li v-for="(item,index) in fenlei" :key="index" @click="fn(item.text)">
