      ],
      fenlei: ""
    };
  },
  methods: {
    get() {
      var api = "data/goodsSorts.txt";
      this.axios.get(api).then(res => {
        this.fenlei = res.data.data;
       
      });
    },
     fn(name) {
      this.$router.push("/tongyong/" + name);
    }
  },
  mounted() {
    this.get();
  }
};
</script>

